.tabsPanel h3 {
    margin-top: 0;
}

.tabsPanel tr td.s3:first-child {
    display: none;
}

.tabsPanel colgroup col:first-child {
    display: none;
}

.tabsPanel tr td.s4 {
    border-left: 1px SOLID #000000;
}

.ritz.grid-container {
    overflow: auto;
}
.ritz .waffle {
    margin: auto;
    min-width: 840px;
}
.waffle {
    /*font-size: 13px;*/
    table-layout: fixed;
    border-collapse: separate;
    border-style: none;
    border-spacing: 0;
    width: 0;
    cursor: default;
    line-height: normal;
}

.waffle td {
    word-wrap: break-word;
    vertical-align: middle;
}

.ritz .waffle a {
    color: inherit;
}

.ritz .waffle .s6 {
    background-color: #fff2cc;
    text-align: left;
    color: #000000;

    
    vertical-align: middle;
    white-space: nowrap;
    direction: ltr;
    padding: 5px;
}

.ritz .waffle .s3 {
    border-bottom: 1px SOLID #000000;
    border-right: 1px SOLID #000000;
    border-left: 1px SOLID #000000;
    background-color: #e9f6ea;
    text-align: right;
    color: #000000;
    
    
    vertical-align: middle;
    white-space: nowrap;
    direction: ltr;
    padding: 5px;
}

